@media (max-width: 740px) {
	.viewTitle {
		font-size: var(--titleFont);
		font-weight: 600;
		padding: 20px 0 1px 20px;
	}
	.views {
		-ms-overflow-style: none; /* for Internet Explorer, Edge */
		scrollbar-width: none; /* for Firefox */
		overflow-x: scroll;
		display: flex;
	}
	.views::-webkit-scrollbar {
		display: none; /* for Chrome, Safari, and Opera */
	}
	.view {
		margin: 1rem 1.5rem 0 1rem;
		padding: 2rem 0;
	}
	.viewImg {
		width: 250px;
		height: 230px;
		border-radius: 20px;
		box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
	}
	.viewImg img {
		width: 100%;
		height: 100%;
		border-radius: 20px;
	}
	.viewLocation {
		color: var(--btnColors);
		padding: 10px 0;
	}
	.viewName {
		font-weight: 600;
	}
	.viewDetails {
		text-align: left;
	}
	.viewFooter {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	.rating {
		display: flex;
		align-items: center;
		padding-top: 10px;
	}
	.ratingIcon {
		margin-right: 1rem;
		color: var(--primaryColor);
	}
	.money {
		font-weight: 600;
	}
}
@media (min-width: 740px), (min-width: 1200px) {
	.viewContainer {
		height: 80vh;
		margin: 0 150px;
	}
	.viewTitle {
		font-size: var(--titleFont);
		font-weight: 600;
		padding: 20px 0 1px 20px;
	}
	.views {
		overflow-x: scroll;
		display: flex;
	}

	.view {
		margin: 1rem 1.5rem 0 1rem;
		padding: 2rem 0;
	}
	.viewImg {
		width: 250px;
		height: 230px;
		border-radius: 20px;
		box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
	}
	.viewImg img {
		width: 100%;
		height: 100%;
		border-radius: 20px;
	}
	.viewLocation {
		color: var(--btnColors);
		padding: 10px 0;
	}
	.viewName {
		font-weight: 600;
	}
	.viewDetails {
		text-align: left;
	}
	.viewFooter {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	.rating {
		display: flex;
		align-items: center;
		padding-top: 10px;
	}
	.ratingIcon {
		margin-right: 1rem;
		color: var(--primaryColor);
	}
	.money {
		font-weight: 600;
	}
}
