@media (max-width: 740px) {
	.similarHousesWrapper {
		display: flex;
		overflow-x: scroll;
		-ms-overflow-style: none; /* for Internet Explorer, Edge */
		scrollbar-width: none; /* for Firefox */
		padding: 0 1rem;
	}
	.similarHouse {
		margin: 1.5rem;
	}
	.similarHouse::-webkit-scrollbar {
		display: none; /* for Chrome, Safari, and Opera */
	}
	.rating {
		margin-top: 5px;
	}
	.similarTitle {
		margin: 0 1.5rem;
		font-weight: bold;
	}
	.similarImage {
		width: 200px;
		height: 150px;
		border-radius: 20px;
	}
	.similarImage img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		border-radius: 20px;
	}
	.similarDescription p {
		padding: 5px 0;
	}
	.similarHouseTitle {
		font-weight: 600;
	}
	.feature {
		color: var(--mainGrey3);
		font-size: 1rem;
	}
	.rateIcon {
		color: var(--primaryColor);
	}
	.money {
		font-weight: bold;
	}
}
@media (min-width: 740px), (min-width: 1200px) {
	.similarContainer {
		margin: 0 40px 50px 40px;
	}
	.similarHousesWrapper {
